
import colors from 'colors';

cc.Class({
    extends: cc.Component,

    properties: {

        scoreTitle:cc.Label

    },

setNumber(number){
    if(number == 0){
        //隐藏这个
        this.scoreTitle.node.active = false;
    }
    this.scoreTitle.string = number;

    if(number in colors){
        this.node.color = colors[number];
    }

},

    start () {

    },

});
